* {
  color: #fcf6f9;
  background-color: #346777;
}

.head {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  background-color: #46c197;
}

.btn--shown {
  width: 15rem;
  height: 3rem;
  border: 1px solid #46c197;
  border-radius: 0.5rem;
  background-color: #346777;
}

.signal--block {
  margin: 0 auto;
  margin-top: 5rem;
  max-width: 1080px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0.5rem;
  gap: 0.25rem;
  border-bottom: 1px solid #46c197;
}